
import { Component, OnInit } from '@angular/core';
import { ContentfulService } from 'src/app/services/contentful.service';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { switchMap } from 'rxjs/operators';




@Component({
  selector: 'app-post',
  template:  `

<div class="columns is-multiline margin-left"  *ngIf="obs$ | async as post">

  <div class="column is-one-quarter">

  <h1 class="title center">{{post.title}}</h1>

  <h6>{{post.descrption}}</h6>
   <p class="subtitle" [innerHtml]="post.body | mdToHtml">

   <p class="button" *ngIf="post.link as link">See mere<a target="_blank" rel="noopener noreferrer" [href]="link">{{link | truncate}}</a></p>
 
 </div>
  <div class="column">

      <figure class="image is-3by3"><img  src="{{ post.img.fields.file.url }}"></figure>
  
      <p class="subtitle"> Oprettet af {{post.author}} 
</div>


  `,
  styles: [`
   .margin-left{
    margin-right:2vw;
    margin-left:2vw;
   
    }
 ` ]
})
export class PostComponent implements OnInit {

  obs$;
  show = true;

  constructor(private cs: ContentfulService, private route: ActivatedRoute) { }

  ngOnInit() {
    /* BEGIN 2.2 */
    this.obs$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) => this.cs.getBlogPost(params.get('id'))
    ));
    /* END 2.2 */
    
    setTimeout(() => {
      this.show = true;
    }, 150);
  }

}
